<template lang="pug">
.scholar__profile
    ul.scholar__info
        li.scholar__profile-photo
            img(src='../../../assets/images/icons/login_scholar.svg').scholar__photo
        li.scholar__profile-row
            .scholar__profile-departments 
                .scholar__profile-name Департаменты:
            ul.scholar__profile-departments-content
                li(v-for='group,idx in scholarGroups').scholar__department {{idx+1}}) {{group.department.name}}
        li.scholar__profile-row
            .scholar__profile-groups 
                .scholar__profile-name Группы:
            ul.scholar__profile-groups-content
                li(v-for='group,idx in scholarGroups').scholar__group {{idx+1}}) {{group.code}}
    .buttons__scholar    
        button(@click="removeCurrentScholar()").remove__scholar-button Удалить данные
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    created() {
        this.fetchScholarGroups(this.scholar.id)
    },
    computed: {
        ...mapState ("scholarGroups", {
            scholarGroups: state => state.scholarGroups
        })
    },
    props: {
        scholar: Object
    },
    methods: {
        ...mapActions("scholarGroups", ["fetchScholarGroups"]),
        ...mapActions("scholars", ["removeScholar"]),
        removeCurrentScholar(){
            this.removeScholar(this.scholar.id)
        }

    }
}
</script>
